import React from 'react'
import Fade from 'react-reveal/Fade'

const Contact = () => {
  return (
    <Fade bottom>
      <div className="contact">
        <h3>Contact</h3>
        <a href="mailto:chuck.curtis.su@gmail.com">
          <img src="https://img.icons8.com/cotton/64/000000/secured-letter.png"/>
        </a>
        <a onClick={() => window.open('https://www.linkedin.com/in/chuck-curtis/', '_blank')}>
          <img src="../../public/In-Blue-128.png"/>
        </a>
      </div>
    </Fade>
  )
}

export default Contact



